import '../../../static/common.css'
export default {
	data() {
		return {
			imgList: [],
			modalName: null,
			textareaAValue: '',
			TabCur: 0,
			labelInfo: [],
			src:'',
		};
	},
	methods: {
		labelInit() {
			let that = this
			that.$request.httpTokenRequest(that.$api.getLabels, that.$api.get, {
				Key: that.$api.Key,
				Client: that.$api.CLIENT,
				Version: that.$api.VERSION,
				labelType: 6
			}).then((res) => {
				if (res.data.flag === 200) {
					that.labelInfo = res.data.data
					that.TabCur = that.labelInfo[0].label_id
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					});
				}
			});
		},
		labelBtm() {
			let that = this
			that.$request.httpTokenRequest(that.$api.villageEdit, that.$api.post, {
				Key: that.$api.Key,
				Client: that.$api.CLIENT,
				Version: that.$api.VERSION,
				villageId: that.villageId,
				lableId:that.TabCur
			}).then((res) => {
				if (res.data.flag === 200) {
						uni.showToast({
						title: res.data.data,
						icon: 'none'
					});
					uni.navigateBack()
		
				} else {
					uni.showToast({
						title: res.data.data,
						icon: 'none'
					});
				}
			});
		},
		tabSelect(id) {
			this.TabCur = id;
		}
	},
	onLoad(option) {
		this.userInfo=uni.getStorageSync('userInfo');
		this.$api.Key=this.userInfo.key
		this.villageId = option.villageId;
		this.labelInit()
	}
}
